.logo{
    margin: 0;
    text-transform: uppercase;
    font-weight: 700;
    font-size: 16px;
    color: white;
}

.navigation {
   padding: 30px 50px;
}

.navigation-items {
    display: flex;
    justify-content: space-between;
}

.navigation-items a {
    text-decoration: none;
    color: white;
    margin: 0 30px;
}

label {
    font-size: 26px;
    display: none;
    width: 100%;
    text-align: right;
    color: white;
}
#toggle {
    display: none;
}

@media only screen and (max-width: 700px) {
    label {
        display: block;
        cursor: pointer;
    }
    .navigation-items {
        text-align: center;
        width: 100%;
        display: none;
    }
    .navigation-items a {
        display: block;
        margin: 0;
        color: white;
        padding-bottom: 20px;
    }
    .logo{
        padding-bottom: 20px;
    }
    #toggle:checked + .navigation-items {
        display: block;
    }
}










