.Header {
    padding: 120px 60px;
}

.Header-button {
    background-color: #111111;
    color: white;
}

.Header-button {
    padding: 15px 30px;
    font-weight: 700;
    margin-right: 30px;
    text-transform: uppercase;
    border: 2px solid white;
}

@media only screen and (max-width: 700px) {
    .Header-button {
        margin-bottom: 20px;
    }

    .Header {
        padding: 70px 60px;
    }
    
}
