html {
  height: 100%;
}

body {
  font-family: 'Raleway', sans-serif;
}

.App {
  height: 100vh;
  background-color: #111111;
}

h1, h2, h3, h4, h5 {
  margin: 0;
}

.spacer{
  padding: 20px;
}

.spacer-large{
  padding: 40px;
}
.spacer-super {
  padding: 60px;
}
.spacer-small{
  padding: 10px;
}
.text-uppercase {
  text-transform: uppercase;
}

.AboutMe{
  background-color: #ff7700;
}

.WhatIDo {
  background-color: white;
}

.text-black {
  color: black;
}

.text-white{
  color: white;
}

.text-orange {
  color: #ff7700;
}

.text-bold {
  font-weight: 700;
}

.text-light {
  font-weight: 300;
}

.Header-text {
  font-size: 65px;
}

.Subheader-text {
  font-size: 55px;
  display: inline;
  padding-bottom: 35px;
}

.border-bottom--orange{
  border-bottom: 1px solid rgb(255, 205, 143);
}

.border-bottom--black{
  border-bottom: 1px solid rgb(160, 160, 160);
}

.border-small {
  width: 600px;
  margin-left: auto;
  margin-right: auto;
}

.Subheader-subtile {
  font-size: 2rem;
}

.title {
  letter-spacing: 2px;
}

.Container-centered {
  margin: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100%;
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

.margin {
  margin: 0 90px;
}

.Container{
  text-align: center;
  padding: 0 60px;
  position: relative;

}

.row {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  /* width: 100%; */
}

.column {
  display: flex;
  flex-direction: column;
  flex-basis: 100%;
  flex: 1;
}

.column-item {
  /* background-color: blue; */
  padding: 20px;
  text-align: left;
  display: flex;
}

.image {
  width: 300px;
  height: 200px;
  padding: 10px;
}

.Skills {
  margin-left: 200px;
  margin-right: 200px;
}

.icon-space {
  margin-right: 30px;
}
.text-cont {
  margin-top: 20px;
}

.Footer {
  text-align: center;
  padding: 80px;
  background-color: #e6e6e6;
}

.Footer-icons {
  padding: 10px;
}

.text-grey{
  color: rgb(164, 164, 164);
}

.navigation-items--links{
  list-style: none;
  margin: 0;
  display: flex;
  color: white;
}

.navigation-items--links-items{
  padding-left: 10px;
}


@media only screen and (max-width: 800px) {
  .Header-text {
    font-size: 30px;
  }
  .spacer{
    padding: 15px;
  }
  .Subheader-text{
    font-size: 30px;
  }
  
  .Subheader-subtile{
    font-size: 1em;
  }

  .Skills {
    margin: 0;
  }

  .border-small{
    width: 300px;
  }

  .Skills-title{
    font-size: 18px;
  }

  .image {
    width: 200px;
    height: 150px;
  }
}